import { Button } from "@/components/ui/button"

export function ContactSection() {
  return (
    <section className="mt-32 mb-32">
      <div className="mx-auto max-w-4xl text-center">
        <h2 className="text-3xl font-bold sm:text-4xl">Contact Us</h2>
        <p className="mt-4 text-lg text-muted-foreground">
          Have questions or want to purchase a subscription? Get in touch with us.
        </p>

        <div className="mt-8 flex flex-col items-center gap-4">
          <p className="text-sm text-muted-foreground">
            Telegram: <span className="font-medium">@your_admin_username</span>
          </p>

          <Button asChild size="lg">
            <a
              href="https://t.me/your_admin_username"
              target="_blank"
              rel="noopener noreferrer"
            >
              Contact via Telegram
            </a>
          </Button>
        </div>
      </div>
    </section>
  )
}
